<template>
	<div id="sdbox">
		<ul class="ul">
			<li v-for="(x,y) in simg" :style="{background: 'url(./src/'+x+')',backgroundSize:'cover'}" :class="clas[y]" @click="dj(clas[y])"></li>
		</ul>
	</div>
</template>

<script>
	
	export default{
		name:"sdbox",
		props:['sdimg'],
		data(){
			return{
				simg:[],
				clas:[]
			}
		},
		mounted(){
			this.simg=this.sdimg;
			for(let i=0;i<this.simg.length;i++){
				this.clas.push('a'+i)
			}
		},
		methods:{
			dj(b){
				var cla=Math.ceil(this.clas.length/2)
				var b=Number(b.substr(1))+1;
				if (b<cla) {
					this.clas.push(this.clas.shift());
				}else if(b>cla){
					this.clas.unshift(this.clas.pop());
				}
			}
		}
	}	
	
</script>

<style scoped>
	
#sdbox{
	width:100%;
	height:auto;
	margin: 0 auto;
	margin-top: 30px;
	background-color: #f3f3f3;
	padding-top: 100px;
	padding-bottom: 100px;
}	

.ul {
  position: relative;
  z-index: 2;
  height: 200px;
  transform-style: preserve-3d;
  perspective: 800px;
}
.ul li {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 300px;
  height: 200px;
  margin-left: -150px;
  margin-top: -100px;
  border-radius: 8px;
  transition: transform 0.5s ease-in-out;
}


.a0{transform: translateX(-300px) translateZ(100px) rotateY(30deg)}
.a1{transform: translateX(-150px) translateZ(200px) rotateY(30deg)}
.a2{transform: translateZ(300px)}
.a3{transform: translateX(150px) translateZ(200px) rotateY(-30deg)}
.a4{transform: translateX(300px) translateZ(100px) rotateY(-30deg)}
	
</style>


