<template>
	<div id="ljbox">
		<span class="span span1" @click="left()"><&nbsp;</span>
		<span class="span span2" @click="right()">&nbsp;></span>
		<ul class="ul" :style="{transition:tran,width:ulw+'%',marginLeft:ulmar+'%'}">
			<li v-for="(x,y) in limg" :style="{width:liw+'%',filter:'blur('+fil+')'}">
				<img :src="'./src/'+x" :style="{width:imgw+'%',height:imgh+'%',transform: 'scale('+imgsc+')'}"/>
			</li>
		</ul>
	</div>
</template>

<script>
	
	export default{
		name:"ljbox",
		props:['ljimg'],
		data(){
			return{
				limg:[],
				ulw:0,
				liw:0,
				ulmar:0,
				tran:'all 0.5s ease',
				fil:'0px',
				imgw:'60',
				imgh:'100',
				imgsc:'1,1'
			}
		},
		mounted(){
			this.limg=this.ljimg;
			this.ulw=this.limg.length*100
			this.liw=100/this.limg.length
		},
		methods:{
			left(){
				if(this.ulmar!=-(this.limg.length-1)*100){
					this.fil='5px'
					this.imgw='70'
					this.imgh='98'
					this.imgsc='1.35, 0.85'
				}
				if (this.ulmar<-(this.limg.length-2)*100) {
					this.ulmar=0
					this.tran='none'
				}else{
					this.tran='all 0.5s ease'
					this.ulmar-=100
				}
				setTimeout(()=>{
					this.fil='0px'
					this.imgw='60'
					this.imgh='100'
					this.imgsc='1,1'
				},500)
			},	
			right(){
				if(this.ulmar!=0){
					this.fil='5px'
					this.imgw='70'
					this.imgh='98'
					this.imgsc='1.35, 0.85'
				}
				if (this.ulmar>=0) {
					this.tran='none'
					this.ulmar=-(this.limg.length-1)*100
				} else{
					this.tran='all 0.5s ease'
					this.ulmar+=100
				}
				setTimeout(()=>{
					this.fil='0px'
					this.imgw='60'
					this.imgh='100'
					this.imgsc='1,1'
				},500)
			}
		}
	}	
	
</script>

<style scoped>
	
#ljbox{
	width:100%;
	height:450px;
	margin: 0 auto;
	margin-top: 30px;
	padding-top: 50px;
	padding-bottom: 50px;
	background:linear-gradient(45deg,#5D60AD,#5E57A2 30%,#FA3DAC);
	overflow: hidden;
	position: relative;
}	
.span{
	position: absolute;
    top: 50%;
    z-index: 10;
    width: 60px;
    height: 60px;
    background-color: #000;
    transform: translateY(-50%);
    color: #fff;
    text-align: center;
    line-height: 60px;
    font-size: 24px;
    transition: all 0.25s ease;
    cursor: pointer;
}
.span1{left: 0;}
.span1:hover{margin-left: -5px;}
.span2{right: 0;}
.span2:hover{margin-right: -5px;}


.ul{
	height: 100%;
    display: block;
    float: left;
}	
.ul li{
	height: 100%;
	padding: 0 250px;
	box-sizing: border-box;
	float: left;
	position: relative;
}
.ul li img{
	filter: drop-shadow(0px 10px 40px rgba(0, 0, 0, 0.55));
	transition: all 0.5s ease;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	/*filter:brightness(0.5)*/
	/*transform: scale(1.35, 0.85)*/
}
</style>


