<template>
	<div id="Zzbox">
		<div class="div">
			<div class="aa">
				<div class="ad">
				<div class="adiv" :style="{transition:tran,width:adiv+'%',marginLeft:(ulmar)+'%'}">
					<div v-for="(x,y) in limg" :style="{width:zdiv+'%'}">
						<p class="p2">{{x.top}}</p>
						<img :src="'./src/'+x.img"/>
						<p class="p1">{{x.bot}}</p>
					</div>
				</div>
				</div>
			</div>
			<span class="span span1" @click="left()"><img src="../assets/img/a.png"/></span>
			<span class="span span2" @click="right()"><img src="../assets/img/a.png" class="a"/></span>
		</div>
		<div class="div1">
			<div class="ac">
			<div class="adiv" :style="{transition:tran,width:adiv+'%',marginLeft:ulmar+'%'}">
				<div v-for="(x,y) in limg1" :style="{width:zdiv+'%'}">
					<img :src="'./src/'+x.img"/>
				</div>
			</div>
			</div>
		</div>
		<div class="div2">
			<div class="ac">
			<div class="adiv" :style="{transition:tran,width:adiv+'%',marginLeft:ulmar+'%'}">
				<div v-for="(x,y) in limg2" :style="{width:zdiv+'%'}">
					<img :src="'./src/'+x.img"/>
				</div>
			</div>
			</div>
		</div>
	</div>
</template>

<script>
	
	export default{
		name:"Zzbox",
		props:['zzimg'],
		data(){
			return{
				limg:[],
				limg1:[],
				limg2:[],
				adiv:0,
				zdiv:0,
				ulmar:0,
				tran:'all 0.5s ease'
			}
		},
		mounted(){
			this.limg=this.zzimg.aa;
			this.limg1=this.zzimg.bb;
			this.limg2=this.zzimg.cc;
			this.adiv=this.limg.length*100
			this.zdiv=100/this.limg.length
		},
		methods:{
			left(){
				this.fil='3px'
				if (this.ulmar<-(this.limg.length-2)*100) {
					this.ulmar=0
					this.tran='none'
				}else{
					this.tran='all 0.5s ease'
					this.ulmar-=100
				}
			},
			right(){
				if (this.ulmar>=0) {
					this.tran='none'
					this.ulmar=-(this.limg.length-1)*100
				} else{
					this.tran='all 0.5s ease'
					this.ulmar+=100
				}
			}
		}
	}	
	
</script>

<style scoped>
	
#Zzbox{
	width:100%;
	height: 480px;
	margin: 0 auto;
	margin-top: 30px;
	background:#0b0b0b;
	overflow: hidden;
	position: relative;
}
.div{
	width: 100%;
	height: 480px;
	background-color:rgba(0,0,0,0.5);
	z-index: 3;
	position: absolute;
	left: 0;
	right: 0;
	margin: auto;
}
.aa{
	width: 665px;
	height: 420px;
	position: relative;
	background:linear-gradient(180deg,#3c3b3c,#807d7c 30%,#cccccc);
	left: 0;
	right: 0;
	margin: auto;
	margin-top: 15px;
	box-sizing: border-box;
	overflow: hidden;
}	
.ad{
	width: 645px;
	height: 400px;
	position: relative;
	left: 0;
	right: 0;
	margin: auto;
	box-sizing: border-box;
	margin: 10px;
	overflow: hidden;
}	
.adiv{
	float: left;
	height: 100%;
}
.adiv div{
	height: 100%;
	text-align: center;
	box-sizing: border-box;
	float: left;
}
.adiv div:hover .p1{height: 40px;z-index: 6;}
.adiv div:hover img{margin-top: 40px;}
.adiv div img{
	width: 100%;
	height: 100%;
	z-index: 3;
	transition: all 0.5s;
}
.adiv div p{
	font-size: 14px;
	height: 0px;
	width: 100%;
	left: 0;
	line-height: 40px;
	transition: all 0.5s;
}
.p1{
	position: absolute;
	height:40px;
	bottom: 0;
	background:linear-gradient(180deg,#C2C1C1,#cccccc);
	z-index: 5;
}
.div1{
	width: 380px;
	height: 250px;
	background:linear-gradient(180deg,#3c3b3c,#807d7c 30%,#cccccc);
	position: absolute;
	left: 70px;
	top:100px;
	margin: auto;
	margin-top: 15px;
	box-sizing: border-box;
	overflow: hidden;
}
.div2{
	width: 380px;
	height: 250px;
	background:linear-gradient(180deg,#3c3b3c,#807d7c 30%,#cccccc);
	position: absolute;
	right: 70px;
	top:100px;
	margin: auto;
	box-sizing: border-box;
	overflow: hidden;
}
.ac{
	width: 360px;
	height: 230px;
	position: absolute;
	right:0;
	left: 0;
	top:0;
	bottom: 0;
	margin: auto;
	box-sizing: border-box;
	overflow: hidden;
}

.span{
	position:absolute;
	color: #fff;
	font-size: 100px;
	top: 170px;
	cursor: pointer;
}
.span1{
	left: 150px;
}
.span2{
	right: 150px;
}
.a{
	transform:scaleX(-1);
}
</style>


